<template>
	<view class="market">
		<tabTop title='交易市场' :open='false'/>
		<view class="market-top">
			<text class="title">JDT/TCNY</text>
			<text class="number">1.64%</text>
			<myTaps name='查看市场行情' class='taps' color='#fff'/>
		</view>
		<view class="taps-box">
			<view class="taps">
				<view class="taps-title border" :class="{'tapsActive':taps}" @click="checkout(true)">买入</view>
				<view class="taps-title" :class="{'tapsActive':!taps}" @click="checkout(false)">卖出</view>
			</view>
		</view>
		<view class="information">
			<view class="information-left">
				<view class="title">
					<text class="text">盘口</text>
					<text class="text">价格</text>
					<text class="text">数量</text>
				</view>
				<view class="lists">
					<view class="list" v-for="(item,index) in lists.list1" :key='index'>
						<text>{{item.id}}</text>
						<text>{{item.price}}</text>
						<text>{{item.number}}</text>
					</view>
				</view>
				<view class="newTransaction" v-show="bottomTapsNum === 0">
					<text class="newTransaction-left">最新交易</text>
					<text class="newTransaction-right">3.595</text>
				</view>
				<view class="lists">
					<view class="list" v-for="(item,index) in lists.list2" :key='index'>
						<text>{{item.id}}</text>
						<text>{{item.price}}</text>
						<text>{{item.number}}</text>
					</view>
				</view>
				<view class="detailed-box">
					<view class="detailed">
						<view class="detailed-left" @click="onBottomTaps(index)" :class="{bottomTapsActive:bottomTapsNum === index}" v-for="(item,index) in bottomTaps" :key='index'>
							<text>{{item.title}}</text>
						</view>
					</view>
				</view>
			</view>
			
			
			
			<view class="information-right">
				
				<view class="right-taps">
					<view class="right-taps-box" v-for="(item,index) in tapsList" :key='index'>
						<view class="left" @click="onTaps(index)">
							<view class="empty"></view>
							<view class="real" v-show="index == tapsNum"></view>
						</view>
						<view class="right">
							<text>{{item.title}}</text>
						</view>
					</view>
				</view>
				
				<view class="from">
					<view class="from-input">
						<view class="title">价格</view>
						<view class="count" v-show="tapsNum === 0">
							<view class="left" @click="onReduce">-</view>
							<view class="content">{{price}}</view>
							<view class="right" @click="onPlus">+</view>
						</view>
						<text class="low" v-show="tapsNum === 1">市场最低价</text>
					</view>
					<view class="from-input">
						<view class="title">数量</view>
						<view class="input">
							<input class="left" type="text" value="" placeholder="请输入数量" placeholder-style='color:#7A8AAE'/>
							<text class="right">DGF</text>
						</view>
					</view>
				</view>
				
				<view class="usable">
					<view class="left">可用 722.03855 TCNY</view>
					<view class="right">全部</view>
				</view>
				
				<view class="turnover">
					<text class="left">交易额</text>
					<text class="mid">150.245</text>
					<text class="right">TCNY</text>
				</view>
				
				<view class="buy">
					<text class="buy-copy">买入</text>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import tabTop from '@/components/tabTop.vue';
	import myTaps from '@/components/myTaps.vue';
	import nextButton from '@/components/nextButton.vue';
	export default{
		components:{tabTop,myTaps,nextButton},
		data(){
			return{
				taps:true,
				tapsNum:0,
				bottomTapsNum:0,
				price:0,
				bottomTaps:[
					{
						title:'五档'
					},
					{
						title:"明细"
					}
				],
				tapsList:[
					{
						title:"限价",
 					},
					{
						title:"市价"
					}
				],
				lists:{
					list1:[
						{
							id:"5",
							price:'3.965',
							number:'6.0k',
						},
						{
							id:"5",
							price:'3.965',
							number:'6.0k',
						},
						{
							id:"5",
							price:'3.965',
							number:'6.0k',
						},
						{
							id:"5",
							price:'3.965',
							number:'6.0k',
						},
						{
							id:"5",
							price:'3.965',
							number:'6.0k',
						}
					],
					list2:[
						{
							id:"5",
							price:'3.965',
							number:'6.0k',
						},
						{
							id:"5",
							price:'3.965',
							number:'6.0k',
						},
						{
							id:"5",
							price:'3.965',
							number:'6.0k',
						},
						{
							id:"5",
							price:'3.965',
							number:'6.0k',
						},
						{
							id:"5",
							price:'3.965',
							number:'6.0k',
						}
					]
				}
			}
		},
		methods:{
			checkout(bol){
				this.taps = bol;
			},
			onTaps(index){
				this.tapsNum = index;
			},
			onBottomTaps(index){
				this.bottomTapsNum = index;
			},
			onPlus(){
				this.price ++
			},
			onReduce(){
				this.price --
			},
			
		}
	}
</script>

<style lang="scss">
	.market{
		@include levelCen();
		@include bg();
		.market-top{
			@include size(710rpx,121rpx,none);
			background:linear-gradient(90deg,rgba(50,65,99,1) 0%,rgba(10,20,41,1) 100%);
			border-radius: 5rpx;
			display: flex;
			align-items: center;
			box-sizing: border-box;
			padding-left: 60rpx;
			padding-right: 65rpx;
			.title{
				@include font(36rpx,#fff);
			}
			.number{
				margin-left: 11rpx;
				@include font(24rpx,#2DEAB7);
			}
			.taps{
				margin-left: auto;
			}
		}
		
		.taps-box{
