					left: 50%;
					transform: translateX(-50%);
					.detailed{
						@include size(270rpx,100%,none);
						@include font(20rpx,#fff);
						@include cen();
						.detailed-left{
							@include cen();
							@include size(50%,100%,#535F81);
							border-radius: 4rpx;
						}
						.detailed-right{
