								background-image: linear-gradient(#2DE9B7,#2D6FD5);
								border-radius: 50%;
								position: absolute;
								left: 50%;
								top: 50%;
								transform:translate(-50%,-50%);
								z-index: 10;
							}
						}
						.right{
							@include font(24rpx,#fff);
							margin-left: 10rpx;
						}
					}
				}
				
