							@include cen();
							border: 1rpx dashed #5D71A4;
							box-sizing: border-box;
							@include size(286rpx,70rpx,#1D2B49);
							@include font(24rpx,#fff);
						}
						.title{
							@include size(286rpx,83rpx,none);
							@include font(24rpx,#fff);
