								@include cen();
							}
							.content{
								flex: 1;
								box-sizing: border-box;
								border-right: 1rpx solid #5D71A4;
								border-left: 1rpx solid #5D71A4;
								@include cen();
							}
							.right{
								@include size(69rpx,69rpx,#303E61);
								@include cen();
							}
						}
						.input{
							@include size(286rpx,70rpx,#1D2B49);
							border: 1rpx solid #5D71A4;
							box-sizing: border-box;
							@include font(24rpx,#fff);
