							box-sizing: border-box;
							padding-left: 20rpx;
							padding-right: 20rpx;
							align-items: center;
							justify-content: space-between;
							.left{
								height: 100%;
								width: 150rpx;
								@include font(20rpx,#fff);
							}
							.right{
								@include font(20rpx,#fff)
							}
						}
						
					}
