		.tapsActive{
			background: linear-gradient(to right,#2A65BC,#079D74);
		}
		.tapsActive2{
			// background: linear-gradient(to right,#2A65BC,#079D74);
		}
		.bottomTapsActive{
			background: #3B4769 !important;
		}
	}
</style>
